.App-grid__photoHolder {
  display: -ms-flexbox;
  display: flex;
  height: 350px;
  width: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-grid__modalPhoto {
  display: -ms-flexbox;
  display: flex;
  height: 350px;
  width: 700px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.modal {
  display: -ms-flexbox;
  display: flex;
}

.right_modal {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
}

.right_modal > button {
  border: none;
  font-size: 1em;
  cursor: pointer;
}

.likes_modal {
  display: -ms-flexbox;
  display: flex;
  margin-top: 10px;
  
}

.photoHeader__modal {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  margin-bottom: 10px;
}

.photoHeader__modal > img {
  margin-right: 10px;
}

.photoFooter_modal {
  margin: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: auto;
}


