.App-photoGrid {
  margin-top: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-grid {
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.App-grid__holder {
  min-width: 25%;
  -webkit-filter: contrast(150%) opacity(80%);
          filter: contrast(150%) opacity(80%);
  margin: 2px;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;

}

.App-grid__holder:hover {
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02);
  -webkit-filter: drop-shadow(5px 0 5px black) contrast(150%);
          filter: drop-shadow(5px 0 5px black) contrast(150%);
  cursor: pointer;

}

@media screen and (max-width: 900px) and (min-width: 700px) {
  
  .App-grid__holder {
    min-width: 40%;
  
  }
}

@media screen and (max-width: 699px) {
  
  .App-grid__holder {
    min-width: 100%;
  
  }
}




